
.employee-details {
  padding: 20px;
  text-align: center;
}

.employee-details h2 {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
  color: var(--text-color-light, #333); 
}


.employee-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  justify-items: center;
  opacity: 0;
  animation: fadeIn 1s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


.employee-card {
  background-color: var(--card-bg-color, #f9f9f9); 
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  width: 100%;
  max-width: 300px;
  transform: translateY(20px);
  opacity: 0;
  animation: cardIn 0.5s ease-in-out forwards;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease;
}

@keyframes cardIn {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.employee-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
}


.employee-info {
  margin-bottom: 20px;
}

.employee-info h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: var(--text-color-light, #333);
}

.employee-info p {
  font-size: 1rem;
  margin: 5px 0;
  color: #808080;
}


.delete-btn {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.delete-btn:hover {
  background-color: #e60000;
  transform: scale(1.05);
}

.delete-btn:focus {
  outline: 2px solid #ff4d4d;
  outline-offset: 4px;
}


body[data-theme='dark'] .employee-details h2 {
  color: var(--text-color-dark, #fff); 
}

body[data-theme='dark'] .employee-card {
  background-color: var(--card-bg-dark, #333); 
  color: var(--text-color-dark, #ffffffff);
}

body[data-theme='dark'] .employee-info h3 {
  color: var(--text-color-dark, #fff);
}

body[data-theme='dark'] .delete-btn {
  background-color: #ff4d4d;
  color: white;
}

body[data-theme='dark'] .delete-btn:hover {
  background-color: #e60000;
}

body[data-theme='dark'] .employee-info p{
  color: var(--text-color-dark, #fff);
}
