.settings {
    padding: 20px;
  }
  
  .settings-section {
    margin-bottom: 30px;
  }
  
  .settings-section h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
    font-size: 1rem;
  }
  
  input[type='radio'] {
    margin-right: 10px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  input[type='text'],
  input[type='email'] {
    width: 100%;
    padding: 8px;
    font-size: 1rem;
    margin-top: 5px;
  }
  
  button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
    opacity: 0.9;
  }
  :root {
    --bg-color: #ffffff;
    --text-color: #000000;
    --primary-color: #007bff;
    --secondary-bg-color: #f8f9fa;
  }
  
  
  [data-theme='dark'] {
    --bg-color: #181818;
    --text-color: #ffffff;
    --primary-color: #1e90ff;
    --secondary-bg-color: #242424;
  }
  
  
  [data-theme='custom'] {
    --bg-color: #f4e2d8;
    --text-color: #4a4a4a;
    --primary-color: #ff6347;
    --secondary-bg-color: #ffe5d5;
  }
  

  body {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  button {
    background-color: var(--primary-color);
    color: var(--text-color);
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  
  