
.register-employee {
  padding: 20px;
  background-color: var(--bg-color-light, #f9f9f9); 
  border-radius: 8px;
  max-width: 500px;
  margin: 20px auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.8);
  font-family: 'Arial', sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease; 
}

.register-employee h2 {
  text-align: center;
  color: var(--text-color-light, #333);
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  font-weight: bold;
  color: var(--text-color-light, #434343); 
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
  box-sizing: border-box;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  transition: border-color 0.3s ease;
}

.form-group input::placeholder {
  color: #aaa;
}

.form-group input:focus {
  border-color: #007bff;
  outline: none;
}

button {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}


@media (max-width: 600px) {
  .register-employee {
    padding: 15px;
    max-width: 90%;
  }

  button {
    font-size: 14px;
    padding: 10px;
  }
}


.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: var(--modal-bg-light, white);
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  width: 300px;
  animation: fadeInModal 0.3s ease-out;
}

button {
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background-color: #0056b3;
}

@keyframes fadeInModal {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}


body[data-theme='dark'] .register-employee {
  background-color: var(--bg-color-dark, #121212); 
  color: var(--text-color-dark, #ffffff); 
}

body[data-theme='dark'] .register-employee h2 {
  color: var(--text-color-dark, #ffffff); 
}

body[data-theme='dark'] .form-group label {
  color: var(--text-color-dark, #ffffff);
}

body[data-theme='dark'] .form-group input {
  background-color: var(--input-bg-dark, #333); 
  color: var(--text-color-dark, #fff); 
  border: 1px solid #444;
}

body[data-theme='dark'] .form-group input::placeholder {
  color: #aaa;
}

body[data-theme='dark'] .modal-content {
  background: var(--modal-bg-dark, #333);
  color: var(--text-color-dark, #fff);
}
