
.help {
  padding: 20px;
  font-family: 'Arial', sans-serif;
  background-color: var(--bg-color, #f9f9f9); 
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.help h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: var(--primary-color, #007bff); 
  text-align: center;
  animation: fadeIn 1.5s ease-in-out;
}

.help-section {
  margin-bottom: 30px;
  animation: slideIn 1s ease-out;
}

.help-section h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: var(--text-color, #333); 
  cursor: pointer;
  transition: color 0.3s ease, transform 0.3s ease;
}

.help-section h3:hover {
  color: var(--highlight-color, #0056b3);
  transform: translateX(5px);
}

.help-section ul {
  list-style-type: none;
  padding-left: 0;
  margin-top: 10px;
}

.help-section ul li {
  margin-bottom: 10px;
  background-color: var(--card-bg-color, #fff); 
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.help-section ul li:hover {
  background-color: var(--card-hover-bg-color, #e9f4fe); 
  transform: translateX(10px);
}

.help-section ul li strong {
  font-weight: bold;
  color: var(--text-color, #333);
}

.help-section p {
  font-size: 1rem;
  color: var(--text-color, #555);
  margin: 10px 0;
  line-height: 1.6;
}

.help-section a {
  color: var(--primary-color, #007bff);
  text-decoration: none;
  transition: color 0.3s ease;
}

.help-section a:hover {
  text-decoration: underline;
  color: var(--highlight-color, #0056b3);
}

.help-section ul li:not(:last-child) {
  margin-bottom: 15px;
}


@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes slideIn {
  0% { transform: translateY(20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}


body[data-theme='light'] {
  --bg-color: #f9f9f9; 
  --text-color: #333;   
  --primary-color: #007bff; 
  --highlight-color: #0056b3; 
  --card-bg-color: #fff; 
  --card-hover-bg-color: #e9f4fe; 
}


body[data-theme='dark'] {
  --bg-color: #121212; 
  --text-color: #fff;   
  --primary-color: #1e90ff; 
  --highlight-color: #61dafb; 
  --card-bg-color: #2e2e2e; 
  --card-hover-bg-color: #333; 
}

