.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #007bff;
  padding: 22px 20px;
  color: white;
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: bold;
}

.navbar-links {
  list-style: none;
  display: flex;  
  gap: 15px;
  margin: 0;
  padding: 0;
  transition: color 0.3s ease;
}

.navbar-links li {
  display: inline;
}

.navbar-links a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  transition: color 0.3s ease;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

.navbar-links a:hover {
  color: #000000;
  background-color: #ffffff;
}

.navbar-links a:focus{
  color: #000000;
  background-color: #ffffff;
}
.navbar-links a.active{
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.navbar-toggle {
  display: none; 
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  
}

@media (max-width: 768px) {
  .navbar-links {
    display: none; 
    flex-direction: column;
    background-color: #333;
    position: absolute;
    top: 50px;
    right: 10px;
    width: 200px;
    border-radius: 8px;
    padding: 10px;
  }

  .navbar-links.show {
    display: flex;  
  }

  .navbar-toggle {
    display: flex;
    align-items: center;
    justify-content: right;
    background: none;
  }
  
  .navbar {
    position: relative;
  }
}


